import React, { useState, useEffect } from "react";
import { getFromLocalStorage, setInLocalStorage, getFromWorkbookSettings, setInWorkbookSettings } from "../taskpane";
import { useForm } from "react-hook-form";
import QuotaBar from "./QuotaBar";
import { TokenInput } from "./TokenInput";
import Instructions from "./Instructions";
import { ModelProviderSelection } from "./ModelProvider";

type FormInputs = {
  apiKey: string;
  apiHost: string;
  disableCalculationOverride: boolean;
  modelProvider: "groq" | "openai";
};

const App = () => {
  const [needSave, setNeedSave] = useState(false);

  const { register, handleSubmit, setValue, watch } = useForm<FormInputs>({
    defaultValues: {
      apiKey: "",
      disableCalculationOverride: false,
      modelProvider: "openai",
    },
  });

  useEffect(() => {
    setValue("apiKey", getFromLocalStorage("apiKey") || getFromLocalStorage("arborApiToken") || "");

    // Load calculation override setting
    getFromWorkbookSettings("disableCalculationOverride").then((value) => {
      setValue("disableCalculationOverride", value === "true");
    });

    // Load model provider setting
    getFromWorkbookSettings("modelProvider").then((value) => {
      setValue("modelProvider", (value as "groq" | "openai") || "openai");
    });
  }, [setValue]);

  // Watch for form changes to enable/disable save button
  useEffect(() => {
    const subscription = watch(() => setNeedSave(true));
    return () => subscription.unsubscribe();
  }, [watch]);

  const onSubmit = async (data: FormInputs) => {
    try {
      setInLocalStorage("apiKey", data.apiKey);
      await setInWorkbookSettings("disableCalculationOverride", data.disableCalculationOverride.toString());
      await setInWorkbookSettings("modelProvider", data.modelProvider);
      setNeedSave(false);
    } catch (error) {
      console.error("Error saving keys:", error);
    }
  };

  return (
    <div className="py-2 px-2">
      <div className="flex min-h-[100dvh] w-screen max-w-full flex-col">
        <div className="flex flex-1 gap-4 p-0 md:p-4">
          <div className="h-full w-full md:block relative">
            <div className="w-full h-full">
              <div className="sticky top-0 flex h-full w-full shrink-0 grow-0 flex-col gap-2 overflow-auto rounded-lg bg-lightGrey py-4 px-4">
                <a
                  className="w-full flex-0 relative mb-6 block h-8 w-24 my-4 flex items-center justify-center"
                  href="https://arborchat.ai/"
                >
                  <img
                    alt="logo"
                    width="96"
                    height="32"
                    decoding="async"
                    data-nimg="1"
                    style={{ color: "transparent" }}
                    src="/assets/logo.png"
                  />
                </a>
                <form onSubmit={handleSubmit(onSubmit)}>
                  <div className="">
                    <TokenInput
                      id="apiKey"
                      label="Arbor API Key"
                      register={register}
                      className="placeholder-color-[#9A99A1]"
                    />
                    <ModelProviderSelection hide register={register} />
                  </div>
                  {watch("apiKey") && <QuotaBar token={watch("apiKey")} />}
                  <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
                    <label className="font-title text-sm px-4 mb-2 flex gap-2">
                      <input type="checkbox" {...register("disableCalculationOverride")} />
                      <span className="text-xs">Disable Calculation Manual Override</span>
                    </label>
                  </div>
                  <div className="">
                    <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-8">
                      <button
                        type="submit"
                        className={`w-full rounded-full bg-[#162332] px-6 py-4 font-semibold text-[#ffffff] ${!needSave ? "bg-[#646b73]" : ""}`}
                        disabled={!needSave}
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </form>
                <Instructions />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;
