import React from "react";

const Instructions = () => {
  const handleFormulaClick = async (formula: string) => {
    try {
      await Excel.run(async (context) => {
        const range = context.workbook.getSelectedRange();
        const cell = range.getCell(0, 0);
        cell.formulas = [[formula]];
        await context.sync();
        cell.calculate();
      });
    } catch (error) {
      console.error("Error inserting formula:", error);
    }
  };

  return (
    <div className="p-4">
      <h2 className="text-lg font-semibold mb-4">Available Formulas</h2>

      <div className="space-y-4">
        <div className="py-3">
          <code className="font-mono text-black">=ARBOR_SCREEN(query)</code>
          <p className="text-sm text-darkGrey mt-1">Screen companies based on your query</p>
          <p
            className="text-xxs text-royalBlue mt-1 cursor-pointer py-2"
            onClick={() => handleFormulaClick('=ARBOR_SCREEN("Apple Supply Chain Companies Stock")')}
          >
            {'=ARBOR_SCREEN("Apple Supply Chain Companies Stock")'}
          </p>
        </div>

        <div className="py-3">
          <code className="font-mono text-black">=ARBOR_DEEP(ticker, query, [isLongAnswer])</code>
          <p className="text-sm text-darkGrey mt-1">Ask questions about a specific company</p>
          <p
            className="text-xxs text-royalBlue mt-1 cursor-pointer py-2"
            onClick={() =>
              handleFormulaClick('=ARBOR_DEEP("AAPL", "What were the key highlights from their latest earnings call?")')
            }
          >
            {'=ARBOR_DEEP("AAPL", "What were the key highlights from their latest earnings call?")'}
          </p>
        </div>

        <div className="py-3">
          <code className="font-mono text-black">=ABROR_FAST(ticker, query)</code>
          <p className="text-sm text-darkGrey mt-1">Get factual information about a company</p>
          <p
            className="text-xxs text-royalBlue mt-1 cursor-pointer py-2"
            onClick={() => handleFormulaClick('=ABROR_FAST("GOOGL", "Who is their current CEO?")')}
          >
            {'=ABROR_FAST("GOOGL", "Who is their current CEO?")'}
          </p>
        </div>

        <div className="py-3">
          <code className="font-mono text-black">=ARBOR_NUMBER(ticker, query)</code>
          <p className="text-sm text-darkGrey mt-1">Get numerical answers about a company</p>
          <p
            className="text-xxs text-royalBlue mt-1 cursor-pointer py-2"
            onClick={() => handleFormulaClick('=ARBOR_NUMBER("MSFT", "What was their revenue growth rate in 2023?")')}
          >
            {'=ARBOR_NUMBER("MSFT", "What was their revenue growth rate in 2023?")'}
          </p>
        </div>
      </div>
    </div>
  );
};

export default Instructions;
